.book img{
    /* width: 100%; */
    object-fit: cover;

}

.book{
    background-color: #fff;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    position: relative;
}

.book h2{
    margin-top: 1rem;
    font-size: 1rem;
}

.book .box{
    position: absolute;
    top: 0;
    left: 0;
    padding: 0.75rem;
    font-size: 1rem;
    border-top-left-radius: 1rem;
    border-bottom-right-radius: 1rem;
    background-color: #C35600;
    color: white;

}